<template>
  <transition name="fade" mode="in-out">
    <div class="articleDetailsPage">
      <v-Header class="head">
        <span slot='text'>标题不是越长越好的好吗？</span>
        <mt-button icon="more" class='more' @click='showMenu' slot="right"></mt-button>
      </v-Header>
      <div id='articleScrollCon'>
        <div>
          <div class="articleDetailsCon">
            <div class="articleTitleCon">标题不是越长越好的好吗？</div>
            <div class="articleTagCon">号角海洋文化 07-25</div>
            <div class="articleTextCon">
              <img src="https://s33.postimg.cc/acjanzcm7/image.jpg" alt="" srcset="">
              轿车开进增江，一男子溺亡，建议自驾车配备安全锤
    南都讯 每次安全事故，都是一个惨痛的教训。7月19日，一辆深圳牌照的轿车开进了龙门县增江河，人车溺水后，司机有时间报警但没机会自救，结果溺死。如果车内备有一把应急安全锤，或许可以击碎车窗玻璃逃生自救。
              <img src="https://s33.postimg.cc/fp851yvbz/image.jpg" alt="" srcset="">
              据了解，当日下午4点半左右，惠州市志愿者救捞队接到龙门110警情，有一台小汽车落入增江河，人员也随车沉入水底，请求增援。随后，赵喜昌、闫伟、郭成权和林城，迅速驾车赶赴现场。
    抵达现场后，经进一步了解，小汽车沉入距岸边50余米的河底，水深约8米。
            </div>
          </div>
          <div class="giveTheThumbsUp"><div class="addPraiseBtn"></div>153</div>
          <hr/>
          <v-CommentList :CommentList='CommentList'/>
        </div>
      </div>
      <div class="commentInputCon">
        <input type="text" placeholder="快来说点什么吧！">
        <div class='inputBtn' @click="entry"></div>
        <div :class='["addPraiseBtn",isAddPraise?"active2":"active1"]'></div>
      </div>
      <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
      <router-view/>
    </div>
  </transition>
</template>
<script>
import Header from '../../components/header';
import CommentList from '../articleDetails/commentList.vue';
 export default {
    name: '',
    components: {'v-Header':Header,'v-CommentList':CommentList},
    props: {},
    data () {
      return {
        actions:[{name:'编辑',method:this.updatePage},{name:'下架',method:this.reclaim}],
        sheetVisible:false,
        isAddPraise:false,
        CommentList:[
          {
            name:'Kiko要瘦身',
            text:'海底的世界是那么的神秘和让人神往啊！去见所未见，闻所未闻，去认识这个世界！',
            imgUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770',
            other:[{
              name:'| 作者',
              text:'海底的世界是那么的神秘和让人神往啊！去见所未见，闻所未闻，去认识这个世界！',
              imgUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770'
            }],
          },
          {
            name:'Kiko要瘦身',
            text:'海底的世界是那么的神秘和让人神往啊！去见所未见，闻所未闻，去认识这个世界！',
            imgUrl:'https://images.gitee.com/uploads/5/1889405_yangjialiang.png?1533865770'
          }
        ]
      }
    },
    watch: {},
    computed: {},
    created () {
    },
    methods: {
      entry(){
        this.$router.push('commentPage/123');
      },
      showMenu(){
        this.sheetVisible=true;
      },
      updatePage(){
        console.log('编辑');
      },
      reclaim(){
        console.log('下架');
      }
    }
  }
</script>
<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
  .articleDetailsPage{
    width: 100%;
    height: auto;
    overflow: hidden;
    background:white;
    .head{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      .more{
        position:absolute;
        top:0;
        right:0;
        height: 0.7rem;
        width: 0.7rem;
        padding:0;
        background:rgba(0,0,0,0);
        box-shadow:none;
      }
    }
    #articleScrollCon{
      top:0.72rem;
      left:0;
      right:0;
      bottom:0.86rem;
      margin:auto;
      position: absolute;
      overflow-x:hidden;
      overflow-y:scroll;
      -webkit-overflow-scrolling:touch;
      background: white;
    }
    .articleDetailsCon{
      margin-top: 0.23rem;
      .articleTitleCon,.articleTagCon,.articleTextCon{
        width: 6.9rem;
        margin-left: 0.3rem;
      }
      .articleTitleCon{
        font-size: 0.44rem;
        font-weight: bold;
      }
      .articleTagCon{
        font-size: 0.22rem;
        color: #888888;
        margin-top: 0.36rem;
      }
      .articleTextCon{
        font-size: 0.32rem;
        line-height: 0.62rem;
        margin-top: 0.15rem;
        img{
          width: 100%;
          margin-top: 0.27rem;
        }
      }
    }
    .giveTheThumbsUp{
      width: 2.1rem;
      height: 0.72rem;
      margin: 0.6rem auto 0.32rem;
      background: #1797c6;
      border-radius: 0.72rem;
      font-size: 0.32rem;
      line-height: 0.72rem;
      text-align: center;
      color: white;
      display: flex;
      align-items: center;
      justify-content: center;
      .addPraiseBtn{margin-right: 0.08rem;}
    }
    hr{
      height: 0.2rem;
      background-color: #f2f2f2;
    }
    .commentInputCon{
      width: 100%;
      height: 0.86rem;
      position: fixed;
      bottom: 0;
      left: 0;
      background: #f5f5f5;
      input,.inputBtn{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0.16rem;
        margin: auto 0;
        width: 5.7rem;
        height: 0.6rem;
        line-height: 0.6rem;
        border-radius: 0.6rem; 
        font-size: 0.3rem;
        text-indent: 1em;
        &::-webkit-input-placeholder {
          color:rgb(137,137,137);
          text-indent: 1em;
        }
      }
      .addPraiseBtn{
        position: absolute;
        bottom: 0.22rem;
        right: 0.62rem;
      }
    }
  }
</style>




