<template>
    <div class="commentlistsCon">
        <div class="commentHeader">评论</div>
        <div v-for="(item,index) in CommentList" :key='item.id'>
            <div class="commentlist">          
                <div class="headImg" :style="{backgroundImage:'url('+item.imgUrl+')'}"></div>
                <div class="listCon">
                    <div class="name" v-text="item.name"></div>
                    <div class="text" v-text="item.text"></div>
                    <div v-if='item.other'>
                        <div class="otherList" v-for="list in item.other" :key='list.name'>
                            <div class="name" v-text="list.name"></div>
                            <div class="text" v-text="list.text"></div>
                        </div>
                    </div>
                </div>
            </div>
            <hr v-if='index!=CommentList.length-1'/>
        </div>
    </div>
</template>
<script>
export default {
    name:'',
    props:['CommentList'],
    data () {
        return {

        }
    }
}
</script>

<style lang="scss" scoped>
*{
      border: none;
      margin: 0;
      padding: 0;
    }
.commentlistsCon{
    border-bottom: 0.01rem solid rgb(229,229,229); 
    .commentHeader{
        width: 6.9rem;
        height: 0.96rem;
        line-height: 0.96rem;
        font-size: 0.32rem;
        text-align: center;
        color: #555555;
        margin-left: 0.33rem;
        border-bottom: 0.01rem solid rgb(229,229,229);
    }
    hr{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        height: 0.02rem;
        background: #f2f2f2;
    }
    .commentlist{
        width: 6.9rem;
        margin: 0.36rem 0.3rem;
        display: flex;
        .headImg{
            width: 0.72rem;
            height: 0.72rem;
            background-size: 0.72rem 0.72rem;
            background-repeat: no-repeat;
            border-radius: 0.72rem; 
            margin-right: 0.24rem
        }
        .listCon{
            flex: 1;
            .name{
                font-size: 0.28rem;
                color: #1797c6;
            }
            .text{
                font-size: 0.32rem;
                color: #333333;
                line-height: 0.58rem;
                margin: 0.11rem 0 0;
            }
            .otherList{
                margin: 0.11rem 0 0;
            }
        }
    }
}
</style>




